import React from 'react';
import Parallax from '../parallax';
import BasicRisu from '../risu';
import Fade from '../fade';

export default function Milestones({ start }) {
  return (
    <>
      <Parallax factor={0.8} position={start} offset={-50} stay={500}>
        <BasicRisu name="glow" width={130} x={100} />
        <BasicRisu name="glow" width={130} stagger={1} x={30} />
        <BasicRisu name="glow" width={130} x={30} />
      </Parallax>
      <Parallax factor={0.7} position={start} offset={-80} stay={500}>
        <BasicRisu name="sglow" width={90} x={75} />
        <BasicRisu name="sglow" width={90} stagger={1} x={20} />
        <BasicRisu name="sglow" width={90} x={20} />
        <BasicRisu name="sglow" width={90} stagger={1} x={20} />
        <BasicRisu name="sglow" width={90} x={20} />
      </Parallax>
      <Parallax factor={0.6} position={start} offset={-110} stay={500}>
        <BasicRisu name="sglow" width={70} x={50} />
        <BasicRisu name="sglow" width={70} stagger={1} x={10} />
        <BasicRisu name="sglow" width={70} x={10} />
        <BasicRisu name="sglow" width={70} stagger={1} x={10} />
        <BasicRisu name="sglow" width={70} x={10} />
        <BasicRisu name="sglow" width={70} stagger={1} x={10} />
        <BasicRisu name="sglow" width={70} x={10} />
      </Parallax>
      <Fade start={start} end={start + 500} offset={60}>
        <p className="title">記念イベント（仮）</p>
      </Fade>
    </>
  );
}
