import React, { useContext } from 'react';
import Container, { ContainerContext } from './container';
import Intro from './scenes/intro';
import Various from './scenes/various';
import Mosaic from './scenes/mosaic';
import Run, { RunTransition } from './scenes/run';
import Milestones from './scenes/milestones';

function PaddedScene({ scene, start, end, padStart, padEnd, ...props }) {
  padStart = padStart || 400;
  padEnd = padEnd || 400;
  const SceneClass = scene;
  const { y } = useContext(ContainerContext);
  const show = y < (-start + padStart) && y > (-end - padEnd);

  return (
    (show)
    ? <SceneClass start={start} {...props} />
    : null
  );
}

function Matsurisu() {
  return (
    <Container>
      <PaddedScene scene={Intro} start={0} end={1600} />

      <PaddedScene scene={Various} start={1600} end={5600} />

      <PaddedScene scene={Run} start={5800} end={9200} />

      <PaddedScene scene={Milestones} start={8400} end={10000} padStart={800} />

      <PaddedScene scene={RunTransition} start={9200} end={10400} padStart={800} />

      <PaddedScene scene={Milestones} start={10000} end={11600} padStart={800} />

      <PaddedScene scene={RunTransition} start={10800} end={12000} padStart={800} />

      <PaddedScene scene={Milestones} start={11600} end={13200} padStart={800} />

      {/* <PaddedScene scene={Mosaic} start={10000} end={15000} dur={3000} fadeStart={700} fadeDur={1500} /> */}
    </Container>
  ); 
}

export default Matsurisu;
